	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Monomedia","date":"16 Dec 2007","slug":"/2007-12-16-monomedia/","tags":"Y2007"},"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2115490694\" title=\"View 'CIMG0386.JPG' on Flickr.com\"><img src=\"http://farm3.static.flickr.com/2352/2115490694_02fc8c86ac.jpg\" alt=\"CIMG0386.JPG\" border=\"0\" width=\"500\" height=\"375\" /></a></p>\n<p>While we're waiting for a DSL line to attach to our multimedia system, we've been showing a fireplace DVD on the flat screen. Sure we have to chop a lot of wood to keep it going, but it fits in well with the Advent season.</p>\n<p>By the way, if you're lucky, in our McCaf&eacute; you'll get served by the owner herself.</p>\n<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2114713125\" title=\"View 'CIMG0387.JPG' on Flickr.com\"><img src=\"http://farm3.static.flickr.com/2398/2114713125_0013c3111c.jpg\" alt=\"CIMG0387.JPG\" border=\"0\" width=\"500\" height=\"375\" /></a></p>"},"next":{"fields":{"title":"Go Team","date":"15 Dec 2007","slug":"/2007-12-15-go-team/"}},"previous":{"fields":{"title":"December Surprise","date":"17 Dec 2007","slug":"/2007-12-17-december-surprise/"}}},{"node":{"fields":{"title":"Go Team","date":"15 Dec 2007","slug":"/2007-12-15-go-team/","tags":"Y2007"},"html":"<p>For our new McCaf&eacute; we've found the the perfect promotion team, 16 dynamic, active and athletic women. We now the uniform sponsors for the 1st women's <a href=\"http://www.buchholz08-handball.de/\">handball team of TSV Buchholz 08</a>, who play in the 3rd <a href=\"http://www.hamburgerhv.de/\">Hamburg Handball League</a>. Last weekend we officially presented the uniforms in our Gym and Fun.</p>\n<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2111820033\" title=\"View 'CIMG0640' on Flickr.com\"><img src=\"https://farm3.static.flickr.com/2362/2111820033_a55459cf51.jpg\" alt=\"CIMG0640\" border=\"0\" width=\"500\" height=\"375\" /></a></p>\n<p>It would have been cool to have shirts in St. Pauli brown, but the supplier could only offer white with brown text. The league is on winter break, but the season resumes in January.</p>\n<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2112590858\" title=\"View 'CIMG0636' on Flickr.com\"><img src=\"https://farm3.static.flickr.com/2315/2112590858_8848f87984.jpg\" alt=\"CIMG0636\" border=\"0\" width=\"500\" height=\"375\" /></a></p>\n<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2112587038\" title=\"View 'CIMG0634' on Flickr.com\"><img src=\"https://farm3.static.flickr.com/2190/2112587038_0602858085.jpg\" alt=\"CIMG0634\" border=\"0\" width=\"500\" height=\"375\" /></a></p>"},"next":{"fields":{"title":"Flying Saucer","date":"14 Dec 2007","slug":"/2007-12-14-flying-saucer/"}},"previous":{"fields":{"title":"Monomedia","date":"16 Dec 2007","slug":"/2007-12-16-monomedia/"}}},{"node":{"fields":{"title":"Flying Saucer","date":"14 Dec 2007","slug":"/2007-12-14-flying-saucer/","tags":"Y2007"},"html":"<p>We were offline a few days as our DSL line was transferred to, er, the largest telecommunications provider in Germany. But now we're back, and we can present our flying coffee bean, or as Frauke calls it, the McKAf&eacute; <a href=\"http://en.wikipedia.org/wiki/Ford_Ka\">Ford KA</a>. Now appearing daily at busy parking lots and autobahn exits.</p>\n<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2111289620\" title=\"View 'CIMG0381.JPG' on Flickr.com\"><img src=\"https://farm3.static.flickr.com/2324/2111289620_b64664f973.jpg\" alt=\"CIMG0381.JPG\" border=\"0\" width=\"500\" height=\"375\" /></a></p>\n<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2110510525\" title=\"View 'CIMG0380.JPG' on Flickr.com\"><img src=\"https://farm3.static.flickr.com/2305/2110510525_a0452b1504.jpg\" alt=\"CIMG0380.JPG\" border=\"0\" width=\"500\" height=\"375\" /></a></p>\n<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2111290002\" title=\"View 'CIMG0382.JPG' on Flickr.com\"><img src=\"https://farm3.static.flickr.com/2025/2111290002_7f97646687.jpg\" alt=\"CIMG0382.JPG\" border=\"0\" width=\"500\" height=\"375\" /></a></p>"},"next":{"fields":{"title":"Still Cut Off","date":"07 Dec 2007","slug":"/2007-12-07-still-cut-off/"}},"previous":{"fields":{"title":"Go Team","date":"15 Dec 2007","slug":"/2007-12-15-go-team/"}}}],"pathPrefix":"","first":false,"last":false,"index":92,"pageCount":1101}}

